* {
  scrollbar-color: #666 #333;
}

.top-bar {
  background-color: #252525;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  text-align: center;
}

.top-butttons {
  color: white;
  padding: 6px 7px;
  background-color: #0f0f0f;
  width: fit-content;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
  position: relative;
  border-width: 0;
  transition: 0.4s;
}
.top-butttons:hover {
  background-color: rgb(80, 80, 80) /*#006653*/;
}
.a-bit-down {
  height: 40px;
  width: 40px;
}
.logout-wrapper,
.ratings {
  display: flex;
  align-items: center;
  height: 100%;
}
.logout-wrapper {
  margin-right: 100px;
}
.ratings {
  margin-left: 100px;
}
.menu-wrapper {
  display: flex;
  justify-content: center;
  background-color: #0f0f0f;
  height: calc(100vh - 60px);
}
.menu-block {
  width: 60%;
}
.rooms-search-box {
  margin-top: 30px;
}
.search-box {
  display: flex;
  justify-content: center;
  width: 100%;
}
.search-box input {
  background-color: #0f0f0f;
  border-style: solid;
  border-color: #3f3f3f;
  color: white;
  width: 400px;
  padding: 8px 18px;
  font-size: 18px;
  border-radius: 30px;
  border-width: 1.2px;
}

.to-creator-button-wrapper {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: center;
  margin-top: 15px;
}
.to-creator-button-wrapper input {
  width: 175px;
}
@media (orientation: portrait) {
  .top-bar {
    height: 70px;
    h2 {
      font-size: 18px;
    }
  }
  .menu-wrapper {
    height: calc(100vh - 70px);
  }
  .logout-wrapper {
    margin-right: 13px;
  }
  .ratings {
    margin-left: 13px;
  }
  .menu-block {
    width: 95%;
  }
  .search-box input {
    width: 80%;
  }
}
